.header {
  position: fixed;
  z-index: 9;
  background-color: $blackopaciti;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 98px; }


.header__nav {
  display: flex;
  & ul {
    display: flex; }
  &-link {
    margin-right: 20px;
    text-decoration: none;
    position: relative;
    color: white;
    text-decoration: none;
    line-height: 24px; }
  &-link:before, &:after {
    content: '';
    position: absolute;
    transition: transform .5s ease; }
  &:before {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background: $blue;
    transform:  scaleX(0); }
  &:hover:before {
    transform:  scaleX(1); }
  &:last-child {
      margin-right: 0; } }


