.intro {
  position: relative;
  height: 100vh;
  width: 100%;
  background-image: url("../img/bg1.png");
  background-position: 0 0;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $blackopaciti; }

  .container {
    height: 100%;
    display: flex;
    justify-content: center; }

  &__content {
    align-self: center; }

  &__title {
    color: $white;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 30px; }

  &__desc {
    @extend .intro__title;
    text-transform: none; } }









